$Gilroy: 'Gilroy', sans-serif;

$MainText: #000000;
$transition: .3s ease-in-out;

html {
  font-size: 34px;
  overflow-x: hidden;
}

body {
  position: relative;
  min-width: 320px;
  color: $MainText;
  font-family: $Gilroy;
  line-height: 1;
  background: #F5F5F5;

  -webkit-font-smoothing: subpixel-antialiased;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  backface-visibility: hidden;
  text-rendering: geometricPrecision;
}

.clearfix {
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}

* {
  outline: none;
  word-wrap: break-word;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a, a:focus, a:hover {
  text-decoration: none;
}

ol, ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
}

button {
  padding: 0;
  cursor: pointer;
}

input,
button,
textarea {
  border-radius: 0;
  -webkit-appearance: none;
}

input[type=number] {
  -moz-appearance: textfield;

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
  }
}

select {
  -webkit-appearance: none;
}

.container {
  //width: 100%;
  //max-width: 1080px;
  width: 1080px;
  margin: 0 auto;
  padding: 20px;
}

.intro {
  display: flex;
  margin: 0 -2px;

  .logo,
  .info {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 0 4px;
    padding: 29px 21px 38px 21px;
    width: calc(50% - 8px);
    background: #FFFFFF;
    box-shadow: 0 0 96px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
  }

  .info {
    .item {
      width: 100%;
      display: flex;
      padding: 0 0 20px 7px;

      .icon {
        width: 70px;
        height: 70px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
        padding: 15px;
        margin-top: 4px;
        font-size: 32px;
        color: #EABD37;
        border-radius: 50%;
        background: #FFFFFF;
        border: 1px solid #F2F3F5;
        box-sizing: border-box;
      }

      .text {
        flex: auto;
        padding-left: 30px;

        h3 {
          margin: 0;
          font-weight: 400;
          font-size: 42px;
          line-height: 1.2;
          text-transform: uppercase;
          color: #333333;
        }

        p {
          margin: 0;
          font-weight: 400;
          font-size: 27px;
          line-height: 1.2;
          color: #333333;
        }
      }

      &:last-child {
        padding-bottom: 0;
      }
    }
  }
}

.company-list {
  margin: 20px 0;

  .title {
    padding: 32px 15px;
    margin-bottom: 20px;
    background: #FFFFFF;
    box-shadow: 0 0 96px rgba(0, 0, 0, 0.1);
    border-radius: 25px;

    h1 {
      margin: 0;
      padding-bottom: 6px;
      font-weight: 500;
      font-size: 34px;
      line-height: 1.12;
      text-align: center;
      text-transform: uppercase;
      color: #333333;
    }
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    //flex-direction: column;
    padding: 5px;
    counter-reset: list;
    background: #FFFFFF;
    box-shadow: 0 0 96px rgba(0, 0, 0, 0.1);
    border-radius: 25px;

    li {
      width: calc(50% - 20px);
      min-height: 247px;
      margin: 10px;
      padding: 20px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      background: #F5F5F5;
      border-radius: 25px;

      .data {
        width: 100%;
        display: flex;
        align-items: center;
      }

      .counter,
      .info {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
        padding: 15px;
        font-weight: 500;
        font-size: 32px;
        line-height: 1;
        text-transform: uppercase;
        color: #333333;
        background: #FFFFFF;
        border-radius: 40px;

        .icon-info {
          color: #EABD37;
        }
      }

      .counter {
        width: 80px;
        height: 80px;
        box-shadow: 0 1px 0 rgba(20, 20, 20, 0.1);

        &:before {
          counter-increment: list;
          content: counter(list);
        }
      }

      .info {
        width: 72px;
        height: 72px;
      }

      .name {
        width: calc(100% - 80px);
        //width: 28%;
        padding-left: 18px;

        p {
          margin: 0;
          font-weight: normal;
          font-size: 34px;
          line-height: 1;
          color: #333333;
        }
      }

      .logo {
        width: 100%;
        padding: 11px 0;
        text-align: center;
      }

    }
  }
}

.contacts {
  display: flex;
  margin: 0 -2px;

  .item {
    display: flex;
    align-items: center;
    margin: 0 4px;
    padding: 12px 30px 9px 30px;
    width: calc(50% - 8px);
    background: #FFFFFF;
    box-shadow: 0 0 96px rgba(0, 0, 0, 0.1);
    border-radius: 25px;

    .icon {
      width: 70px;
      height: 70px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      padding: 15px;
      //margin-top: 12px;
      font-size: 32px;
      color: #EABD37;
      border-radius: 50%;
      background: #FFFFFF;
      border: 1px solid #F2F3F5;
      box-sizing: border-box;
    }

    .text {
      flex: auto;
      padding-left: 30px;
      margin-bottom: 6px;

      p {
        margin: 0;
        display: flex;
        align-items: center;
        font-weight: normal;
        font-size: 26px;
        line-height: 1.1;
        color: #333333;

        a {
          margin: 0 0 0 10px;
        }

        span {
          display: block;
          flex: auto;
          margin: 0 0 1px -7px;
        }

        .icon-apple,
        .icon-android {
          display: block;
          font-size: 36px;
          padding: 5px 0 0 0;
        }
      }

      a {
        display: block;
        margin-top: 7px;
        font-weight: 500;
        font-size: 48px;
        line-height: 1.1;
        color: #333333;

        &:hover {
          color: #EABD37;
        }
      }
    }

    .qr {
      max-width: 102px;
      margin-left: 23px;

      img {
        mix-blend-mode: luminosity;
      }
    }
  }
}
